import { Component, OnInit } from '@angular/core';
import { PackagesServices } from '../global/packages.services';
import { ROUTER } from '../global/router';
import { GLOBALS } from '../global/globals'; // Global Service
@Component({
  selector: 'nepal-packages',
  templateUrl: './nepal.packages.component.html',
  styleUrls: ['./nepal.packages.component.less'],
  providers:[PackagesServices]
})
export class NepalPackagesComponent implements OnInit {

  allPackages:any;
  packages:any;
  packageNight:number = 4;
  startingNight:number = 4;
  pkgLbl:any = GLOBALS.packagesLabel;
  router:any = ROUTER;
  

  constructor(private packageService: PackagesServices)
  {
       this.packageService.getKashmirPackages()
          .subscribe(
          (res) => {
             this.allPackages = res;
             this.packages = this.allPackages.packages;
          },
          (error) => console.log("error : " + error),
          () => console.log('Error in GetApplication in Login : ' + Error)
      );
  
  }

  ngOnInit() {
  }

}



