import {Component} from '@angular/core';

@Component({
  selector: 'ngbd-tabset-pills',
  template: `
  <ngb-tabset type="pills">
  <ngb-tab title="Flights">
    <ng-template ngbTabContent>
        <div id="menu2" class="tab-pane">
            <form>
              <ul class="tabConetentBox">
                <li>
                  <label>
                    <select class="ddlFlight">
                      <option value="SF" selected="">Select Flight</option>
                      <option value="AI">Air India</option>
                      <option value="9W">Jet Airways</option>
                      <option value="UK">Vistara</option>
                      <option value="6E">IndiGo</option>
                      <option value="SG">SpiceJet </option>
                      <option value="G8">GoAir </option>
                      <option value="I5">AirAsia </option>
                      <option value="OP">Air Pegasus </option>
                      <option value="LB">Air Costa </option>
                      <option value="2T">TruJet </option>
                    </select>
                  </label>
                </li>
                <li>
                  <label class="cal">
                    <input type="text" class="txtDeparture" placeholder="Departure">
                  </label>
                </li>

                <li>
                  <label class="plan">
                    <input type="text" class="txtdestination" placeholder="Destination">
                  </label>
                </li>
                <li>
                  <label>
                    <select class="ddlClass">
                      <option selected="" value="C">Class</option>
                      <option value="E" class="economy_class">Economy</option>
                      <option value="PE" class="premium_class">Premium Economy</option>
                      <option value="B" class="business_class">Business</option>
                    </select>
                  </label>
                </li>
                <li>
                  <span class="search-btn searchchFlight" data-toggle="modal" data-target="#search-popup">search now</span>
                </li>
              </ul>
            </form>
          </div>
    </ng-template>
  </ngb-tab>

  <ngb-tab>
    <ng-template ngbTabTitle><b>Hotels</b></ng-template>
    <ng-template ngbTabContent>
        <div id="menu3" class="tab-pane">
            <form>
              <ul class="tabConetentBox">
                <li>
                  <label class="h-icon1">
                    <input type="text" class="txtHotelname" placeholder="Hotel Name">
                  </label>

                </li>
                <li>
                  <label class="cal">
                    <input type="text" class="txtCheckinDate" placeholder="Check-in">
                  </label>
                </li>
                <li>
                  <label class="cal">
                    <input type="text" class="txtCheckoutDate" placeholder="Check-out">
                  </label>
                </li>
                <li>
                  <label>
                    <select class="ddlRoom">
                      <option selected="" value="R">Room</option>
                      <option>1</option>
                      <option>2</option>
                      <option>3</option>
                      <option>4</option>
                    </select>
                  </label>
                </li>
                <li>
                  <span class="search-btn" data-toggle="modal" data-target="#search-popup">search now</span>
                </li>
              </ul>
            </form>
          </div>
    </ng-template>
  </ngb-tab>

  <ngb-tab title="holidays" >
    <ng-template ngbTabContent>
        <div id="menu4" class="tab-pane">
            <form>
              <ul class="tabConetentBox">
                <li>
                  <label>
                    <select class="ddlSource">
                      <option value="S" selected="">Source</option>
                      <option>Agartala</option>
                      <option>Agra</option>
                      <option>Agumbe</option>
                      <option>Ahmedabad</option>
                      <option>Aizawl</option>
                      <option>Ajmer</option>
                      <option>Alappuzha Beach</option>
                      <option>Allahabad</option>
                      <option>Alleppey</option>
                      <option>Almora</option>

                    </select>
                  </label>
                </li>
                <li>
                  <label>
                    <select class="ddlDestination">
                      <option value="D" selected="">Destination</option>
                      <option value="AL">Alabama</option>
                      <option value="AK">Alaska</option>
                      <option value="AZ">Arizona</option>
                      <option value="AR">Arkansas</option>
                      <option value="CA">California</option>
                      <option value="CO">Colorado</option>
                      <option value="CT">Connecticut</option>
                      <option value="DE">Delaware</option>
                      <option value="DC">Dist of Columbia</option>
                      <option value="FL">Florida</option>
                      <option value="GA">Georgia</option>
                      <option value="HI">Hawaii</option>
                      <option value="ID">Idaho</option>
                      <option value="IL">Illinois</option>
                      <option value="IN">Indiana</option>
                      <option value="IA">Iowa</option>
                      <option value="KS">Kansas</option>
                      <option value="KY">Kentucky</option>

                    </select>
                  </label>
                </li>


                <li>
                  <label>
                    <select class="ddlMonthOfTravel">
                      <option selected="" value="M">Month of travel</option>
                      <option>March 2016</option>
                      <option>April 2016</option>
                      <option>May 2016</option>
                      <option>June 2016</option>
                      <option>July 2016</option>
                      <option>August 2016</option>
                      <option>September 2016</option>
                      <option>October 2016</option>
                      <option>November 2016</option>
                      <option>December 2016</option>

                    </select>
                  </label>
                </li>
                <li>
                  <span class="search-btn" data-toggle="modal" data-target="#search-popup">search now</span>
                </li>
              </ul>
            </form>
          </div>
    </ng-template>
  </ngb-tab>
</ngb-tabset>
`
  //templateUrl: '../tabset-pills.html'
})
export class NgbdTabsetPills { }


