import { HomeComponent } from '../home/home.component';
import { AboutComponent } from '../about/about.component';
import { ContactComponent } from '../contact/contact.component';
import { PackagesComponent } from '../packages/packages.component';
import { HotelsComponent } from '../hotels/hotels.component';
import { DestinationComponent } from '../destination/destination.component';
import { PageNotFoundComponent } from '../extra/pageNotFound.component';

// nepal packages

import { NepalPackagesComponent } from '../packages/nepal.packages.component';
import { NepalPackageComponent_2n } from '../packages/nepal-packages/nepal.packages.2n';
import { NepalPackageComponent_3n } from '../packages/nepal-packages/nepal.packages.3n';
import { NepalPackageComponent_4n } from '../packages/nepal-packages/nepal.packages.4n';

import { KashmirPackages } from '../packages/kashmir-packages/kashmir.packages';
import { GtPackages } from '../packages/gt-packages/gt.packages';

export const ROUTER = [
  {
    path: 'home',
    component: HomeComponent,
    navTitle: 'Home',
    visibleOnMain: true
  },
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full',
    navTitle: '',
    visibleOnMain: false
  },

  {
    path: 'about',
    component: AboutComponent,
    navTitle: 'About ',
    visibleOnMain: true
  },
  {
    path: 'hotels',
    component: HotelsComponent,
    navTitle: 'Hotels ',
    visibleOnMain: true
  },
  {
    path: 'packages',
    component: PackagesComponent,
    navTitle: 'Packages ',
    visibleOnMain: true
  },
  {
    path: 'destination',
    component: DestinationComponent,
    navTitle: 'Destination ',
    visibleOnMain: true
  },
  {
    path: 'contact',
    component: ContactComponent,
    navTitle: 'Contact ',
    visibleOnMain: true
  },
  {
    path: 'nepal-packages',
    component: NepalPackagesComponent,
    navTitle: 'Nepal Packages ',
    visibleOnMain: false,
    hasChild: false,
    children: [
      {
        path: 'nepal-package-2n',
        component: NepalPackageComponent_2n,
        navTitle: '02 Night 03 Days',
        position: 'subMenu',
        visibleOnMain: false
      },
      {
        path: 'nepal-package-3n',
        component: NepalPackageComponent_3n,
        navTitle: '03 Night 04 Days',
        position: 'subMenu',
        visibleOnMain: false
      } ,
      {
        path: 'nepal-package-4n',
        component: NepalPackageComponent_4n,
        navTitle: '04 Night 05 Days',
        position: 'subMenu',
        visibleOnMain: false
      }
    ]
  }, 
  
  {
    path: 'kashmir-packages',
    component: KashmirPackages,
    navTitle: 'Kashmir Packages',
    position: 'subMenu',
    visibleOnMain: false
  },
  {
    path: 'golden-triangle-packages',
    component: GtPackages,
    navTitle: 'Golden Triangle Packages',
    position: 'subMenu',
    visibleOnMain: false
  },
  
  {
    path: '**',
    component: PageNotFoundComponent,
    visibleOnMain: true
  }

];


