import { Component } from '@angular/core';
import { PackagesServices } from '../../global/packages.services';
import { GLOBALS } from '../../global/globals'; // Global
@Component({
    selector: 'nepal-package-3n',
    templateUrl: './nepal.packages.3n.html',
    providers: [PackagesServices]
})

export class NepalPackageComponent_3n {
    allPackages: any;
    pkg: any;
    packageNight: number = 3;
    startingNight: number = 2;
    pkgLbl: any = GLOBALS.packagesLabel;
    constructor(private packageService: PackagesServices) {

        this.packageService.getPackages()
            .subscribe(
            (res) => {
                this.allPackages = res;
                this.pkg = this.allPackages.packages[this.packageNight - this.startingNight];
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
            );

    }
}


