// Additional Modules
import { AngularFontAwesomeModule } from 'angular-font-awesome/angular-font-awesome';

import { AppComponent } from './app.component';
import { HeaderComponent } from './global/header.component';
import { FooterComponent } from './global/footer.component';

// ROUTER FILE
import { ROUTER } from './global/router';

import { SliderComponent } from './global/slider/slider.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { PackagesComponent } from './packages/packages.component';
import { HotelsComponent } from './hotels/hotels.component';
import { DestinationComponent } from './destination/destination.component';
import { PageNotFoundComponent } from './extra/pageNotFound.component';

import { PackagesMenu } from './global/packages/packages.menu.component';
import { KashmirMenu } from './global/packages/kashmir.menu.component';
import { GtMenu } from './global/packages/gt.menu.component';


import { NepalPackagesComponent } from './packages/nepal.packages.component';
import { NepalPackageComponent_2n } from './packages/nepal-packages/nepal.packages.2n';
import { NepalPackageComponent_3n } from './packages/nepal-packages/nepal.packages.3n';
import { NepalPackageComponent_4n } from './packages/nepal-packages/nepal.packages.4n';

import { KashmirPackages } from './packages/kashmir-packages/kashmir.packages';
import { GtPackages } from './packages/gt-packages/gt.packages';


// Extras
import { NgbdTabsetPills } from './global/tabs/tabset.pills';
import { NgbdCarouselConfig } from './global/carousel/carousel.config';



@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    SliderComponent,
    HomeComponent,
    AboutComponent,
    ContactComponent,
    PackagesComponent,
    HotelsComponent,
    DestinationComponent,
    PageNotFoundComponent,

    NgbdTabsetPills,
    NgbdCarouselConfig,

    PackagesMenu,
    KashmirMenu,
    GtMenu,
    NepalPackagesComponent,
    NepalPackageComponent_2n,
    NepalPackageComponent_3n,
    NepalPackageComponent_4n,

    KashmirPackages,
    GtPackages
  ],
  imports: [
    HttpModule,
    BrowserModule,
    FormsModule,
    AngularFontAwesomeModule,
    NgbModule.forRoot(),
    RouterModule.forRoot(ROUTER)
  ],
  providers: [Title],
  bootstrap: [AppComponent]
})
export class AppModule { 
 // routerUrls:any = ROUTER[0].path;

  constructor(){
   // console.log(this.routerUrls);
  }
}



