import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';
import {Observable} from 'rxjs';
import 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';

@Injectable()

export class PackagesServices {
    people:any;
    dataRoot:string = "assets/data/";

    // Packages Data File
    nepalPackages:string=this.dataRoot+"nepal.packages.json";
    kashmirPackages:string=this.dataRoot+"kashmir.packages.json";
    gtPackages:string=this.dataRoot+"golden.triangle.packages.json";

    constructor(private http: Http) {
    }
    
    // Nepal Packages
    getPackages = (): Observable<Response> => {
        return this.http.get(this.nepalPackages).map(res => res.json());
    }

    // Kashmir Packages
    getKashmirPackages = (): Observable<Response> => {
        return this.http.get(this.kashmirPackages).map(res => res.json());
    }


     // Kashmir Packages
     getGTPackages = (): Observable<Response> => {
        return this.http.get(this.gtPackages).map(res => res.json());
    }

}


