import { Component, Input, ElementRef  } from '@angular/core';
import { PackagesServices } from '../../global/packages.services';
import { GLOBALS } from '../../global/globals'; // Global Service
@Component({
    selector: 'gt-menu',
    templateUrl: './gt.menu.component.html',
    providers:[PackagesServices]
})

export class GtMenu {
    allPackages:any;
    pkg:any;
    packageNight:number = 5;
    startingNight:number = 5;
    pkgLbl:any = GLOBALS.packagesLabel;

    constructor(private packageService: PackagesServices,elementRef:ElementRef)
    {
         this.packageService.getGTPackages()
            .subscribe(
            (res) => {
               this.allPackages = res;
               this.pkg = this.allPackages.packages[0];
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
        );
    
    }
}


