import { Component } from '@angular/core';
import { PackagesServices } from '../../global/packages.services';
import { GLOBALS } from '../../global/globals'; // Global
@Component({
    selector: 'gt-packages',
    templateUrl: './gt.packages.html',
    providers: [PackagesServices]
})
export class GtPackages {
    currentJustify = 'start';
    allPackages: any;
    pkgs: any;
    packageNight: number = 4;
    startingNight: number = 4;
    pkgLbl: any = GLOBALS.packagesLabel;
    pkgsTitle:string;
    pkgsInclusion:any;
    constructor(private packageService: PackagesServices) {
       
        this.packageService.getGTPackages()
            .subscribe(
            (res) => {
                this.allPackages = res;
                this.pkgs = this.allPackages.packages;
                this.pkgsTitle = this.allPackages.packagesTitle;
                var allInclu = this.pkgs;

                var pkgLength = this.pkgs.length;
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
            );
    }

    // Change Package Title
    changePkgTitle(title){
        this.pkgsTitle = title;
    }
}


